import React, { useState } from "react";
import './CustomerDetails.css';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import axios from 'axios';
import TextField from '@material-ui/core/TextField';


export interface IService {
    id: number
    serviceType: string,
    price: number
}
export interface IBusiness {
    id: number,
    serviceTypeId: number,
    nameCustomer: string,
    phoneCustomer: string,
    emailCustomer: string,
    dateBussins: Date
}
export default function CustomerDetails() {

    const [dataService, setDataService] = useState<IService[]>([]);

    const data = axios.get(`http://localhost:3000/service`)
        .then(res => {
            const allService = res.data;
            setDataService(allService)
        })


    const [chooseBusiness, setChooseBusiness] = useState(0);
    const [open, setOpen] = useState(true);
    const [displayCustomerdetails, setDisplayCustomerdetails] = useState(false);
    const [idCount, setidCount] = useState(1);
    const [dateMitting, setdateMitting] = useState<IBusiness[]>();
    const dataMeeting = axios.get(`http://localhost:3000/business`)
        .then(res => {
            const allMeeting = res.data;
            setdateMitting(allMeeting)
        })
    const addBusiness = (event: any) => {
        // if (dateMitting?.find((b) => b.dateBussins == event.target.date.value)) {debugger
        //     alert("the date not good"+chooseBusiness);
    //    }
    //    else{
        debugger
        event.preventDefault();
        const newbusiness: IBusiness = {
            id: idCount, serviceTypeId: chooseBusiness, nameCustomer: event.target.name.value,
            phoneCustomer: event.target.phone.value, emailCustomer: event.target.email.value, dateBussins: event.target.date.value
        };
        axios
            .post('http://localhost:3000/business/', newbusiness)
            .then(() => {
                setDisplayCustomerdetails(false);
                setChooseBusiness(0);
                setidCount((idCount + 1))
            })
            .catch(err => {
                console.error(err);
            });//}
            alert("the date not good"+chooseBusiness);
    }

    const chekService = (event: React.ChangeEvent<HTMLInputElement>) => {
        setChooseBusiness(Number((event.target as HTMLInputElement).value));
    };

    if (!open) {
        return (<Button variant="outlined"
            onClick={(o) => setOpen(true)}>
            ניהול פרטי עסק
        </Button>)
    }
    else {
        return (
            <div>
                <Dialog
                    fullScreen
                    open={open}
                    onClose={(o) => setOpen(false)}>
                    <AppBar sx={{ position: 'relative' }}>
                        <Toolbar>
                            <IconButton
                                edge="start"
                                color="inherit"
                                onClick={(o) => setOpen(false)}
                                aria-label="close">
                                <CloseIcon />
                            </IconButton>
                            <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
                            </Typography>
                            <Button autoFocus color="inherit" onClick={(o) => setOpen(false)}>
                                שמור
                            </Button>
                        </Toolbar>
                    </AppBar>
                    <FormControl component="fieldset" >
                        <FormLabel component="legend">שרותי העסק:</FormLabel>
                        <RadioGroup aria-label="gender" name="gender1" value={chooseBusiness} onChange={chekService}>
                            <ul>{dataService.map(s => <FormControlLabel key={s.id} value={s.id} control={<Radio />} label={s.serviceType} />)}</ul>
                        </RadioGroup>
                    </FormControl>
                    <p>{dataService.filter((t) => t.id === chooseBusiness).map((t) => t.price)} </p>
                    {chooseBusiness != 0 ?
                        <form onSubmit={addBusiness} >
                            <TextField
                                id="datetime-local"
                                label="תאריך עסקה"
                                name="date"
                                type="datetime-local"
                                // defaultValue="new Date(yyyy-MM-ddThh:mm)"
                                InputLabelProps={{
                                    shrink: true,
                                }} />
                            <Button variant="outlined" color="secondary" onClick={(d) => setDisplayCustomerdetails(true)}>להזמנה</Button>
                            {displayCustomerdetails ?
                                <div >
                                    <TextField id="outlined-basic" label="שם מלא" name="name" variant="outlined" required />
                                    <TextField id="outlined-basic" label="פלאפון" name="phone" variant="outlined" required />
                                    <TextField id="outlined-basic" label="מייל" name="email" variant="outlined" required />
                                    <Button variant="outlined" color="secondary" type="submit">לסיום הזמנה</Button>
                                </div> : null}
                        </form> : null}
                </Dialog>
            </div>
        );
    }
}